import React from "react";
import {__} from "@wordpress/i18n";
import { FormTokenField, PanelBody, ToggleControl } from "@wordpress/components";
import AabColorPicker from "../../../components/aab-colorpicker";

export default function LabelsPanel({ attributes, setAttributes }) {
  const { enableLabels, labels, labelsTextColor, labelsBgColor } = attributes;
  const aab_premium = aagb_local_object.licensing;

  return (
    <>
      <PanelBody 
        initialOpen={false}
        title={__('Labels', 'advanced-accordion-block')}
        className={!aab_premium ? 'aab-pro-element' : ''}
      >
        <ToggleControl
          label={__('Enable Labels', 'advanced-accordion-block')}
          checked={enableLabels}
          disabled={!aab_premium}
          onChange={(enableLabels) => setAttributes({ enableLabels })}
        />
        {
          enableLabels && (
            <>
              <FormTokenField
                __experimentalAutoSelectFirstMatch
                __experimentalExpandOnFocus
                __next40pxDefaultSize
                __nextHasNoMarginBottom
                label={__(
                  'Add Labels',
                  'advanced-accordion-block'
                )}
                onChange={(labels) => setAttributes({ labels })}
                suggestions={[]}
                value={labels}
                disabled={!aab_premium}
              />
              <AabColorPicker
                label={__('Color', 'advanced-accordion-block')}
                value={labelsTextColor}
                onChange={(labelsTextColor) => setAttributes({labelsTextColor})}
                bgValue={labelsBgColor}
                onBgChange={(labelsBgColor) => setAttributes({labelsBgColor})}
              />
            </>
          )
        }
      </PanelBody>
    </>
  )
}