import React from "react";
import {useBlockProps as useDefaultBlockProps} from '@wordpress/block-editor';

import { getBorderStyle } from "../../utils/get-border-style";
import AccordionHead from "./accordion-head";
import AccordionBody from "./accordion-body";
import {createAttrGetter} from "../../utils/global-settings";

export default function Output(props) {
  const useBlockProps = props.isEditor ? useDefaultBlockProps : useDefaultBlockProps.save;
  const {
    singleAcdId,
    makeActive,
    QaStyle,
    step,
    checkList,
    selectedCategories,
    accessibilityOn
  } = props.attributes;

  const getAttrDeep = createAttrGetter( props.attributes);
  const border = getAttrDeep('border') ?? {};
  const margins = getAttrDeep('margins') ?? {};
  const borderRadius = getAttrDeep('borderRadius') ?? {};

  const borderStyle = getBorderStyle(border);
  const categoryClassList = (selectedCategories || []).map(item => {
    return "cat_" + item
      .toLowerCase() // Convert to lowercase
      .replace(/\s+/g, '-') // Replace spaces with dashes
      .replace(/&/g, 'and') // Replace '&' with 'and'
      .replace(/%/g, 'percent') // Replace '%' with 'percent'
      .replace(/[^a-z0-9-]/g, ''); // Remove all other special characters
  });

  return (
    <>
      <div {...useBlockProps({className: `aagb__accordion_container panel ${categoryClassList.join(" ")} ${step ? 'step' : ''} ${checkList ? 'check-list' : ''} ${makeActive ? 'aagb__accordion_active' : ''} ${QaStyle ? 'style-qa' : ''} `})}
        tabIndex={accessibilityOn ? 0 : -1}
        style={
          {
            ...borderStyle,
            ...(borderRadius && { borderRadius: borderRadius + 'px'}),
            marginTop: `${margins.top}`,
            marginBottom: `${margins.bottom}`,
          }
        }
        id={singleAcdId}
      >
        <AccordionHead {...props} />
        <AccordionBody {...props} />
      </div>
    </>
  )
}