import React from "react";
import { InnerBlocks as DefaultInnerBlocks } from '@wordpress/block-editor';
import { getBlockTypes } from '@wordpress/blocks';
import { getBorderStyle } from "../../utils/get-border-style";
import StepDownIcon from "../../components/icons/step-down";
import {createAttrGetter} from "../../utils/global-settings";

export default function AccordionBody(props) {
  const InnerBlocks = props.isEditor ? DefaultInnerBlocks : DefaultInnerBlocks.Content;
  const EXCLUDED_BLOCKS = ["aab/accordion-item"];
  const ALLOWED_BLOCKS = getBlockTypes().map(block => block.name).filter(blockName => !EXCLUDED_BLOCKS.includes(blockName))
  const aab_premium = aagb_local_object.licensing;

  const {
    makeActive,
    buttonShow,
    contentCount,
    QaStyle,
    step,
    stepText,
    checkList,
    button_show,
    readMoreText,
  } = props.attributes;

  const activeClass = makeActive ? `aagb__accordion_body--show` : '';
  const expandClass = buttonShow ? 'expand' : '';

  const getAttrDeep = createAttrGetter(props.attributes);
  const bodyBorder = getAttrDeep('bodyBorder') ?? {};
  const paddings = getAttrDeep('paddings') ?? {};
  const bodyBg = getAttrDeep('bodyBg') ?? {};
  const borderRadius = getAttrDeep('borderRadius') ?? {};

  const bodyBorderStyle = getBorderStyle(bodyBorder);
  return (
    <>
      <div
        className={`aagb__accordion_body ${activeClass} ${expandClass} `}
        role="region"
        style={{
          borderRadius: borderRadius ? `0 0 ${borderRadius}px ${borderRadius}px` : '0',
          ...(bodyBg && { backgroundColor: bodyBg }),
          ...bodyBorderStyle,
          ...(!QaStyle ? {
              ...(Object.values(paddings).some(val => val) && {
                padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
              })
            } : QaStyle && checkList ? {
              borderTop: 'none !important',
              paddingTop: `0`,
              ...(paddings.bottom && { paddingBottom: paddings.bottom }),
              ...(paddings.right && { paddingRight: paddings.right }),
              paddingLeft: `calc(${paddings.left ?? '15px'} + 140px)`
            } : {
              borderTop: 'none !important',
              paddingTop: `0`,
              ...(paddings.bottom && { paddingBottom: paddings.bottom }),
              ...(paddings.right && { paddingRight: paddings.right }),
              paddingLeft: `calc(${paddings.left ?? '15px'} + 95px)`
            }
          ),
        }}
      >
        <div className={`aagb__accordion_component ${button_show ? "read-more-btn" : ""}`}
          data-contentCount={button_show ? contentCount : null}
        >
          <InnerBlocks
            allowedBlocks={ALLOWED_BLOCKS}
            template={[['core/paragraph', {content: 'Accordion Content'}]]}
            className="aagb__accordion_inner_content"
          />

          {
            !props.isEditor && button_show && (
              <div className="aagb_overlay"
                style={
                  {
                    background: `linear-gradient(to bottom, ${bodyBg}00, ${bodyBg}EE)`
                  }
                }
              />
            )
          }
        </div>

        {
          button_show && aab_premium && (
            <button className="aagb_button_toggle">
              {readMoreText}
            </button>
          )
        }

        {
          step && (
            <div className="continue">
              <span className="step-text">{stepText}</span>
              <span className="step-down-icon">
                <StepDownIcon />
              </span>
            </div>
          )
        }
      </div>
    </>
  )
}