import React from "react";
import {__} from "@wordpress/i18n";
import { RichText as DefaultRichText } from "@wordpress/block-editor";
import CompleteIcon from "../../components/icons/complete";
import { getBorderStyle } from "../../utils/get-border-style";
import {createAttrGetter} from "../../utils/global-settings";

const iconClassesMap = {
  "plus-alt": "dismiss",
  "plus-alt2": "minus",
  "arrow-down": "arrow-up",
  "arrow-down-alt2": "arrow-up-alt2",
  "insert": "remove",
}

export default function AccordionHead({ isEditor, attributes, setAttributes }){ 
  const RichText = isEditor ? DefaultRichText : DefaultRichText.Content;
  const aab_premium = aagb_local_object.licensing;
  const {
    QaStyle,
    makeActive,
    heading,
    showIcon,
    iconClass,
    iconPosition,
    anchorPosition,
    step,
    checkList,
    headingIconImageUrl,
    headingIconAlt,
    showHeadingIcon,
    subheading,
    enableLabels,
    labels,
    labelsTextColor,
    labelsBgColor,
    labelsGlobalTextColor,
    labelsGlobalBgColor,
    accessibilityOn
  } = attributes;
  const getAttrDeep = createAttrGetter(attributes);
  const anchorLinkShow = getAttrDeep('anchorLinkShow') ?? false;
  const paddings = getAttrDeep('paddings') ?? {};
  const headingColor = getAttrDeep('headingColor') ?? null;
  const headingTag = getAttrDeep('headingTag') ?? 'h5';
  const iconColor = getAttrDeep('iconColor') ?? null;
  const iconBackground = getAttrDeep('iconBackground') ?? null;
  const headerBg = getAttrDeep('headerBg') ?? null;
  const aIconText = getAttrDeep('aIconText') ?? 'A';
  const qIconText = getAttrDeep('qIconText') ?? 'Q';
  const qIconColor = getAttrDeep('qIconColor') ?? '';
  const qIconBg = getAttrDeep('qIconBg') ?? '';
  const aIconColor = getAttrDeep('aIconColor') ?? '';
  const aIconBg = getAttrDeep('aIconBg') ?? '';
  const subheadingColor = getAttrDeep('subheadingColor') ?? null;
  const headingBorder = getAttrDeep('headingBorder') ?? { color: null, style: 'solid', width: '0px' };
  const iconFontSize = getAttrDeep('iconFontSize') ?? null;
  const borderRadius = getAttrDeep('borderRadius') ?? {};
  const headingBorderStyle = getBorderStyle(headingBorder);
  const currentIconClass = makeActive ? iconClassesMap[iconClass] : iconClass;
  const calculatedHeaderBg = QaStyle && (headerBg?.toLowerCase() === "#bcb6b638") ? "transparent" : headerBg;
  let subheadingPlaceholder = aab_premium ? "Write some subheading" : "Subheading Available on Pro";
  if(!isEditor) subheadingPlaceholder = "";

  return (
    <div
      className={`aagb__accordion_head ${iconPosition} ${subheading && aab_premium ? 'hasSubHeading' : ''} ${makeActive ? 'aagb__accordion_active' : ''}`}
      data-active={makeActive}
      style={{
        borderRadius: borderRadius ? `${borderRadius}px ${borderRadius}px 0 0` : '0',
        backgroundColor: headerBg ? calculatedHeaderBg : undefined,
        ...(Object.values(paddings).some(v => !!v) && {
          padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
        }),
        ...headingBorderStyle
      }}
    >
      <div className={`aagb__accordion_heading ${iconPosition} ${anchorPosition || ''}`}>
          {
            aab_premium && checkList && (
              <label className="checklist-label">
                <span />
              </label>
            )
          }
          {
            showHeadingIcon && headingIconImageUrl && aab_premium && !QaStyle && (
              <div className="heading-icon">
                <img
                  src={headingIconImageUrl}
                  alt={__(headingIconAlt || 'Heading Icon', 'advanced-accordion-block')}
                />
              </div>
            )
          }

          {QaStyle && aab_premium && (
            <div className="icon-container">
              <div className="icon-q"
                style={
                  {
                    color: qIconColor,
                    backgroundColor: qIconBg
                  }
                }
              >
                {qIconText}
              </div>
              <div
                className="icon-a"
                style={
                  {
                    color: aIconColor,
                    backgroundColor: aIconBg,
                  }
                }
              >
                {aIconText}
              </div>
            </div>
          )}

          <div className="head_content_wrapper">
            <div className="title_wrapper">
              <RichText
                className="aagb__accordion_title"
                tagName={headingTag}
                value={heading}
                onChange={(heading) => setAttributes({heading})}
                style={
                  {
                    margin: 0,
                    color: headingColor,
                  }
                }
              />
              {
                aab_premium && enableLabels && labels?.length ? (
                  <div className="aagb__labels_container">
                    {
                      labels.map(label => {
                        return (
                          <small
                            className="label_name"
                            style={
                              {
                                color: labelsTextColor || labelsGlobalTextColor,
                                backgroundColor: labelsBgColor || labelsGlobalBgColor
                              }
                            }
                          >
                            {label}
                          </small>
                        )
                      })
                    }
                  </div>
                ) : null
              }
              {
                anchorLinkShow && aab_premium && !step && isEditor && (
                  <a tabIndex={accessibilityOn ? 0 : -1} className="anchorjs-link" href="#">
                    <i className="dashicons dashicons-admin-links"></i>
                  </a>
                )
              }
            </div>
            <RichText
              className="aagb__accordion_subheading"
              tagName="p"
              placeholder={subheadingPlaceholder}
              value={aab_premium ? subheading : ''}
              onChange={(value) => {
                setAttributes({subheading: value});
              }}
              onFocus={(e) => {
                if (!aab_premium) {
                  e.target.blur(); // Prevent focus if aab_premium is false
                }
              }}
              style={
                {
                  margin: "5px 0 0 0",
                  color: subheadingColor,
                }
              }
            />
          </div>
      </div>
      {
        showIcon ? (
          <div
            className={`aagb__accordion_icon`}
            style={{
              ...(iconColor && { color: iconColor }),
              ...(iconBackground && { backgroundColor: iconBackground })
            }}
          >
            <div className="aagb__icon_dashicons_box">
              {
                step && (
                  <span className="complete-sign">
                    <CompleteIcon />
                  </span>
                )
              }
              <span
                className={`aagb__icon dashicons dashicons-${currentIconClass}`}
                style={{
                  fontSize: iconFontSize
                      ? iconFontSize + 'px'
                      : '',
                }}
              />
            </div>
          </div>
        ) : step && (
          <span className="complete-sign">
            <CompleteIcon />
          </span>
        )
      }
    </div>
  )
}