import React from "react";
import {__} from "@wordpress/i18n";
import { FormTokenField, PanelBody } from "@wordpress/components";

export default function CategoryFilterPanel({ attributes, setAttributes }) {
  const { categoryList, selectedCategories } = attributes;
  const aab_premium = aagb_local_object.licensing;

  if(!categoryList) return <></>;

  return (
    <>
      <PanelBody
        initialOpen={false}
        title={__('Category Filter', 'advanced-accordion-block')}
      >
        <FormTokenField
          __experimentalAutoSelectFirstMatch={true}
          __experimentalExpandOnFocus={true}
          __next40pxDefaultSize={true}
          __nextHasNoMarginBottom={true}
          __experimentalShowHowTo={false}
          __experimentalValidateInput={input => categoryList.includes(input)}
          label={__(
            'Set Category Tag',
            'advanced-accordion-block'
          )}
          onChange={(state) => {
            setAttributes({selectedCategories: state});
          }}
          className='aab_component_form_token_selector'
          suggestions={categoryList}
          value={selectedCategories}
          disabled={!aab_premium}
        />
        <p className="aab__sublabel">
          {__('(Set tags in the parent block first. They will appear here for selection.)', 'advanced-accordion-block')}
        </p>
      </PanelBody>
    </>
  )
}